import "./AboutMe.css";
import foto from "../../images/foto.jpg";

function AboutMe() {
  return (
    <section className="about-me" id="about-me">
      <div className="about-me__container">
        <h2 className="about-me__title"></h2>
        <div className="about-me__author">
          <div className="about-me__about-author">
            <h3 className="about-me__name"></h3>
            <p className="about-me__age">-, 40 </p>
            <p className="about-me__text">
                   ,    . 
                  .    ,   
              .   .  2015     
              .  ,     -, 
               -     .
            </p>
            <ul className="about-me__links">
              <li>
                <a
                  href="https://github.com/td-diana"
                  className="about-me__link"
                  target="blank"
                >
                  Github
                </a>
              </li>
            </ul>
          </div>
          <img className="about-me__foto" src={foto} alt=" " />
        </div>
      </div>
    </section>
  );
}

export default AboutMe;
