import { useEffect, useContext } from "react";
import { CurrentUserContext } from "../../contexts/CurrentUserContext";
import "./Profile.css";
import FormValidation from "../../validation/formValidation";

function Profile({ handleLogOut, handleProfile }) {
  const { handleChange, resetForm, errors, values, isValid } = FormValidation();
  const currentUser = useContext(CurrentUserContext);

  function handleSubmit(e) {
    e.preventDefault();
    handleProfile(values);
  }

  useEffect(() => {
    if (currentUser) {
      resetForm(currentUser, {}, true);
    }
  }, [currentUser, resetForm]);

  const editUser =
    !isValid ||
    (currentUser.name === values.name && currentUser.email === values.email);

  return (
    <main className="profile">
      <form className="profile__form" name="profile" onSubmit={handleSubmit}>
        <h1 className="profile__title">{`, ${
          currentUser.name || ""
        }!`}</h1>
        <div className="profile__container">
          <label className="profile__label">
            <span className="profile__label-text"></span>
            <input
              name="name"
              className="profile__input"
              onChange={handleChange}
              type="text"
              value={values.name || ""}
              required
              minLength="2"
              maxLength="30"
            />
            <span className="profile__error">{errors.name || ""}</span>
          </label>
          <label className="profile__label">
            <span className="profile__label-text">E-mail</span>
            <input
              name="email"
              className="profile__input"
              onChange={handleChange}
              type="email"
              value={values.email || ""}
              required
            />
            <span className="profile__error">{errors.email || ""}</span>
          </label>
        </div>

        <div className="profile__container-button">
          <>
            {!editUser ? (
              <button
                type="submit"
                disabled={editUser ? true : false}
                className={`profile__button-edit ${
                  editUser ? "profile__button-edit_disabled" : ""
                }`}
              >
                
              </button>
            ) : (
              <button
                title="  "
                type="submit"
                disabled={editUser ? true : false}
                className={`profile__button-edit ${
                  editUser ? "profile__button-edit_disabled" : ""
                }`}
              >
                
              </button>
            )}
          </>

          <button
            type="submit"
            className="profile__button-exit"
            onClick={handleLogOut}
          >
              
          </button>
        </div>
      </form>
    </main>
  );
}

export default Profile;

